import { useLocation } from 'react-router-dom';
import { NavigationWrapper, StyledNavLink } from './NavigationOverlay.styled';
import IconArrow from 'images/icons/IconArrow';

const NavigationOverlay = () => {
  const location = useLocation();
  console.log('location', location);

  return (
    <NavigationWrapper>
      <StyledNavLink to="/"></StyledNavLink>
      <IconArrow />
      {location.pathname === '/forWomen' && (
        <StyledNavLink to="/forWomen"> </StyledNavLink>
      )}
      {location.pathname === '/forMen' && (
        <StyledNavLink to="/forMen"> </StyledNavLink>
      )}
      {location.pathname === '/forChildren' && (
        <StyledNavLink to="/forChildren"> </StyledNavLink>
      )}
      {location.pathname === '/decorAndToys' && (
        <StyledNavLink to="/decorAndToys">  </StyledNavLink>
      )}
      {location.pathname === '/aboutUs' && (
        <StyledNavLink to="/aboutUs"> </StyledNavLink>
      )}
    </NavigationWrapper>
  );
};

export default NavigationOverlay;
